import React from 'react'
import Logo from '../CommonComponents/Logo/Logo'
import codeLogo from '../../assets/images/codeLogo.png'
import { HomeContainer, CardsContainer } from './Home_Styles'
import SectionCard from './Card/Card'

function Home () {
  return (
    <HomeContainer className='Home'>
      <h1>Welcome to My Website</h1>
      <h2 className='Home-description'>
        <Logo src={codeLogo} alt='CodeLogo' width={30} height={30} />
        <p> Hello! I'm Luciano.</p>
      </h2>
      <p>This is my personal page. Here you can find information about me, my projects, and my contact information.</p>
      <CardsContainer className='Cards-Container'>
        <SectionCard Title='About' Subtitle='Know me more' Text='' Link='/about' />
        <SectionCard Title='Projects' Subtitle='What i made?' Text='' Link='/projects' />
        <SectionCard Title='Contact' Subtitle='Keep in touch' Text='' Link='/contact' />
      </CardsContainer>
    </HomeContainer>
  )
}

export default Home
