import Logo from '../Logo/Logo'
import codeLogo from '../../../assets/images/codeLogo.png'
import Container from 'react-bootstrap/Container'
import Nav from 'react-bootstrap/Nav'
import Navbar from 'react-bootstrap/Navbar'
import './NavBar.css'
import { NavLink } from 'react-router-dom'

export default function NavBar () {
  return (
    <Navbar expand='lg' className='bg-body-tertiary Custom-Nabar' style={{ fontSize: '18px' }}>
      <Container>
        <Navbar.Brand href='/home'>
          <Logo src={codeLogo} alt='CodeLogo' width={35} height={30} />
        </Navbar.Brand>
        <Navbar.Toggle aria-controls='basic-navbar-nav' />
        <Navbar.Collapse id='basic-navbar-nav'>
          <Nav className='me-auto'>
            <Nav.Link as={NavLink} to='/' className='Links'>
              Home
            </Nav.Link>
            <Nav.Link as={NavLink} to='/about'>
              About
            </Nav.Link>
            <Nav.Link as={NavLink} to='/projects'>
              Projects
            </Nav.Link>
            <Nav.Link as={NavLink} to='/contact'>
              Contact
            </Nav.Link>
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  )
}
